$redcolor: #901c1c;
$orangecolor: #d3573c;
$bordercolor: #c4c4c4;
$modalbackground: #db8876;
$font: calibri;

* {
  font-family: $font;
}

a {
  text-decoration: none;
}

body {
  position: relative;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
  flex-direction: column;
}

.modal {
  margin: auto;
  position: absolute;
  background-color: $modalbackground;
  height: 30em;
  width: 22em;
  position: fixed;
  border-radius: 5px;
  padding: 1em;
  display: flex;
  flex-direction: column;
  z-index: 2;
  margin-left: 30%;
  top: 10vh;
  animation: boxIn 0.3s;
}

.modal p {
  font-size: 28px;
  margin: 0;
}

.fa {
  color: white;
  font-size: 34px;
  position: absolute;
  cursor: pointer;
  margin-left: 85%;
}

.modal p:nth-child(2) {
  margin-bottom: 20px;
}

.modal label,
.text-control {
  display: block;
}

.modal label {
  font-size: 22px;
}

.text-control {
  width: 100%;
  border-radius: 5px;
  border: none;
  padding-bottom: 1px;
  height: 35px;
}

textarea {
  width: 100%;
  border-radius: 5px;
}

.send_button {
  background-color: $redcolor;
  color: white;
  border: none;
  border-radius: 5px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 18px;
  padding-right: 18px;
  margin-top: 20px;
  cursor: pointer;
}

.head {
  width: 100%;
  padding-top: 0.3em;
  margin-bottom: 1em;
}

.logo {
  width: 17em;
  margin-right: 4em;
}

#profile {
  display: flex;
  flex-direction: row;
  width: 50%;
  padding-left: 3.5em;
  width: auto;
  height: 250px;
}

.infos {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 50%;
  height: 100%;
  margin: 0;
}

h1 {
  color: $orangecolor;
  font-size: 2.3em;
  margin: 0;
}

.city {
  color: $redcolor;
  font-size: 20px;
}

.slogan {
  color: #525252;
}

#tagsbox {
  height: 40px;
}

.btn_contact {
  border-radius: 5px;
  background-color: $redcolor;
  padding-top: 15px;
  padding-bottom: 15px;
  color: white;
  cursor: pointer;
  border: 0;
  font-size: 20px;
  margin-left: 10%;
  width: auto;
}

.tagsbox {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 350px;
}

.btn_tags {
  border: 0.5px solid $bordercolor;
  box-sizing: border-box;
  border-radius: 11px;
  background-color: white;
  color: $redcolor;
  margin-left: 0.5em;
  cursor: pointer;
  font-size: 0.9em;
  text-transform: capitalize;
  &:hover {
    background-color: $redcolor;
    color: white;
  }
}

.photo_graphers {
  height: 15em;
  width: 15em;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.format_photos {
  width: 23em;
}

#sortby {
  display: flex;
  flex-direction: row;
}

.select-style {
  padding: 0;
  margin: 0;
  width: 100px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

.select-style select {
  padding: 5px 8px;
  width: 100%;
  border: none;
  box-shadow: none;
  background-color: $redcolor;
  appearance: none;
  color: white;
  margin-left: 1em;
  border-radius: 5px;
}

.select-style:after {
  top: 50%;
  left: 85%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-top-color: white;
  border-width: 5px;
  margin-top: -2px;
}

.select-style select:focus {
  outline: none;
}

#medias_sections {
  max-width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.media_box {
  width: 350px;
  height: 340px;
  margin: 25px;
}

.media_box img {
  min-width: 350px;
  position: absolute;
  min-height: 300px;
  max-height: 400px;
  object-fit: cover;
}

.media_format {
  border-radius: 5px;
  width: 350px;
  height: 300px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
}

.vignette-video {
  height: 300px;
  overflow: hidden;
}

.medias_infos {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  background-color: white;
  width: 350px;
  color: $redcolor;
  padding-top: 10px;
  justify-content: space-between;
}

.medias_infos p {
  margin: 0;
  font-size: 22px;
}

.titlebox {
  width: 65%;
  overflow: hidden;
  border: black;
}

.medias_infos p:nth-child(1) {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.medias_infos p:nth-child(2) {
  position: absolute;
  margin-left: 240px;
}

.likesbox {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.heartmedia {
  position: relative;
  top: 0.2em;
  font-size: 20px;
  margin-left: 5px;
  cursor: pointer;
}

#compteur {
  z-index: 2;
  position: fixed;
  background-color: $modalbackground;
  width: 200px;
  height: 50px;
  border-radius: 5px 5px 0px 0px;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-right: 10px;
  justify-content: space-between;
  padding-right: 30px;
  padding-left: 30px;
  align-items: center;
  font-size: 20px;
}

.price {
  margin-left: 50px;
}

#lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  z-index: 100;
  animation: boxIn 0.5s;
  display: none;
  padding-top: 20px;
}

.lightbox_next,
.lightbox_prev {
  font-size: 36px;
  position: fixed;
  top: 50%;
  right: 20px;
  margin-top: 14px;
  z-index: 101;
  cursor: pointer;
  color: $redcolor;
}

.lightbox_prev {
  right: auto;
  left: 20px;
}

.lightbox_close {
  cursor: pointer;
  color: $redcolor;
  font-size: 36px;
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 101;
}

.lightbox_container img {
  max-height: 90vh;
  max-width: 90vh;
  height: auto;
  animation: boxIn 0.3s;
}

.lightbox_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0 auto;
}

@keyframes boxIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.text-lightbox {
  color: $redcolor;
  font-size: 22px;
  margin-top: 10px;
}

.video-lightbox {
  width: 800px;
}

/*Query Smartphone*/
@media screen and (max-width: 840px) {
  body {
    margin: 0 auto;
  }

  #bloc_page {
    margin: 0 auto;
  }

  .logo {
    height: 42px;
    width: 150px;
  }

  .btn_contact {
    position: absolute;
    bottom: 4vh;
    position: fixed;
    z-index: 2;
    width: 180px;
    height: 30px;
    padding: 0;
    font-size: 22px;
    left: 20%;
  }

  h1 {
    font-size: 36px;
    height: 40px;
    width: 320px;
    font-weight: normal;
  }

  .infos {
    height: 130px;
  }

  .infos p {
    font-size: 13px;
  }

  .slogan {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  #compteur {
    display: none;
  }

  #profile {
    padding: 0;
    height: 150px;
    width: 330px;
  }

  #profile p {
    margin: 0;
  }

  .photo_graphers {
    height: 5.5em;
    width: 5.5em;
    position: absolute;
    left: 68%;
  }

  .tagsbox {
    flex-wrap: wrap;
    width: 260px;
    margin-top: 4px;
  }

  .btn_tags {
    border: hidden;
    font-size: 13px;
    font-weight: bold;
    margin-right: 2px;
    margin-left: 0;
  }

  #tagsbox {
    display: none;
  }

  .format_photos {
    width: 9em;
  }

  #sortby {
    display: none;
  }

  #medias_sections {
    max-width: 700px;
  }

  .media_format {
    height: 190px;
    width: 330px;
  }

  .medias_infos {
    width: 330px;
  }

  .price {
    display: none;
  }

  .titlebox p,
  .likesbox p,
  .heartmedia {
    font-size: 14px;
  }

  section {
    width: 330px;
  }

  .media_box {
    width: 330px;
    height: 227px;
    margin: 0px;
  }

  .modal {
    margin: 5%;
    position: absolute;
  }

  .lightbox_container img {
    max-width: 80%;
    min-height: 50vh;
    max-height: 80vh;
  }

  .lightbox_container video {
    width: 300px;
  }
}
